
import { defineComponent, ref, unref } from 'vue'
import {
  IonButton,
  IonLabel,
  IonInput,
  IonItem,
  useIonRouter,
} from '@ionic/vue'
import { supabase } from '@/api'
import PageLayout from '@/components/PageLayout.vue'
import { notify } from '@/notify'

export default defineComponent({
  name: 'CustomerCreate',
  components: {
    PageLayout,
    IonButton,
    IonLabel,
    IonInput,
    IonItem,
  },
  setup() {
    const dogname = ref('')
    const name = ref('')
    const ionRouter = useIonRouter()

    async function createCustomer() {
      const { error } = await supabase.from('customers').insert([
        {
          dogname: unref(dogname),
          name: unref(name),
        },
      ])
      if (error) {
        notify.error('Fehler beim Hinzuf√ºgen des Kunden.', error)
        return
      }
      notify.success('Kunde erfolgreich hinzugef√ºgt.')
      dogname.value = ''
      name.value = ''
      ionRouter.navigate('/tabs/customer', 'back', 'push')
    }

    return {
      dogname,
      name,
      createCustomer,
    }
  },
})
